body {
  overflow-y: auto;
}

.root {
  margin-top: 8vmax;
  width: 100vw;
  height: fit-content;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.root h1 {
  color: #ccfcfc;
  font-weight: 900;
  font-style: italic;
  font-size: max(35px, 3vmax);
  margin-top: 0;
  margin-bottom: 0;
}

.root h2 {
  font-size: 1.5vmax;
}

.root p {
  font-size: max(17px, 1vmax);
  padding: 0 4vmax;
  text-align: justify;
  margin-top: 0;
}

.root hr {
  width: 100%;
  opacity: 0.85;
  margin: 2vmax 0;
  border-bottom-width: 3px;
  border-radius: 3px;
}

.root article {
  color: white;
  background-color: rgba(0, 0, 0, 0.25);
  width: 85vw;
  height: fit-content;
  padding: 3vmax;
  border-radius: 40px;
}

@media screen and (max-width: 650px) {
  .root {
    margin-top: 18vmax;
  }

  .root p {
    padding: 0;
  }
}

@media screen and (max-width: 910px) {
  .root h2 {
    font-size: 3.5vmax;
  }
}

@media screen and (max-width: 1100px) {
  .root {
    margin-top: 13vmax;
  }

  .root p {
    font-size: max(17px, 1.4vmax);
    padding: 0 2vmax;
    padding-right: 30px;
  }
}

@media screen and (max-width: 1400px) {
  .root h2 {
    font-size: 2.75vmax;
  }
}
