.nav-root {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100vw;
  height: 6vmax;
  min-height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.7),
    rgba(0, 0, 0, 0)
  );
}

.logo {
  margin-left: 5vw;
  height: 5vmax;
  min-height: 50px;
}

.logo-s {
  stroke-width: 3px !important;
}

.logo-v {
  stroke-width: 1px !important;
}

.nav-items {
  display: flex;
  justify-content: space-between;
  width: 25vw;
  margin-right: 5vw;
  font-size: max(15px, 1.25vmax);
}

.active {
  font-style: italic;
  color: #ccfcfc;
}

@media screen and (max-width: 1300px) {
  .nav-root {
    height: 80px;
  }

  .logo {
    height: 60px;
  }

  .logo-s {
    stroke-width: 3px !important;
  }

  .logo-v {
    stroke-width: 1.5px !important;
  }

  .nav-items {
    width: 60vw;
    max-width: 350px;
    font-size: 18px;
  }

  @media screen and (max-width: 400px) {
    .logo {
      height: 40px;
    }

    .nav-items {
      font-size: 15px;
    }
  }
}
