.logo-v {
  white-space: pre;
  vector-effect: non-scaling-stroke;
  fill: none;
  stroke-width: 5px;
}

.logo-s {
  fill: none;
  filter: none;
  vector-effect: non-scaling-stroke;
  stroke-linecap: round;
  stroke-width: 18px;
}

.white {
  stroke: rgb(255, 255, 255);
}

.black {
  stroke: rgb(0, 0, 0);
}
