.fallbackBackground, .backdrop {
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.fallbackBackground canvas {
  position: fixed;
  z-index: 0;
}

.fallbackBackground {
  background: linear-gradient(#131a21, #1a2d2d);
}

.backdrop {
  background-color: black;
  opacity: 0.4;
  z-index: 1;
}

.fade {
  transition: opacity 750ms ease-in;
}
