.job-article-root h2 {
  font-style: italic;
  font-weight: 900;
  color: #ccfcfc;
  margin-top: 3vmax;
  font-size: max(20px, 1.5vmax);
}

.job-article-root h3 {
  font-size: max(17px, 1vmax);
}

.job-data,
.job-article-root h2 {
  margin-bottom: 0.5vmax;
}

.job-description {
  margin-top: 2vmax !important;
  margin-bottom: 3vmax !important;
  padding-left: 1vmax !important;
}

.go-back-link {
  font-size: max(17px, 0.75vmax);
}

.job-next-prev-links {
  display: inline-flex;
  justify-content: space-between;
  width: max(250px, 10vmax);
  padding: 0 !important;
  margin-top: 2vmax !important;
}

.job-responsibilities-list {
  list-style-type: disc;
}

.responsibility {
  margin-bottom: max(5px, 0.8vmax);
  font-size: max(16px, 1vmax);
}
