body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

b {
  font-weight: 900;
  font-style: italic;
  color: #e5fcfc;
}

h1 {
  font-weight: 400;
}

ul,
ol {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #9cc1c1;
  transition: color 250ms;
}

a:hover {
  color: #ccfcfc;
}

html * {
  font-family: "Playfair Display", sans-serif !important;
}
