.footer {
  width: 100vw;
  height: fit-content;
  padding: 1vh 0;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.7));
  text-align: center;
  color: white;
  opacity: 0.75;
}

.socials-icon {
  height: 100%;
  fill: #9cc1c1;
  transition: fill 250ms;
  padding: 0 1vh;
  width: 3vmax;
}

.socials-icon:hover {
  fill: #ccfcfc;
}

.socials-wrapper {
  width: 100vw;
  height: 5vh;
  display: flex;
  justify-content: center;
}
